::-webkit-scrollbar {
  width: 0.5rem;
  display: none;
}
@media (min-width: 1024px) {
  ::-webkit-scrollbar {
    display: block;
  }
}

::-webkit-scrollbar-thumb {
  background: hsl(217deg, 33%, 17%);
  border-radius: 0.5rem;
}
::-webkit-scrollbar-thumb:hover {
  background: hsla(225deg, 20%, 61%, 0.5);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  list-style: none;
  color: hsl(210deg, 40%, 98%);
  background-color: transparent;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Heebo", sans-serif;
  background-color: hsl(222deg, 46%, 11%);
  width: 100%;
}
body.scroll__lock {
  overflow: hidden;
}

img {
  display: block;
}

input,
textarea {
  padding: 0.5rem;
  border: 1px solid;
  border-color: hsl(214deg, 20%, 34%);
  color: hsl(215deg, 20%, 65%);
  border-radius: 0.5rem;
  transition: all 0.2s ease;
}
input:focus,
textarea:focus {
  color: hsl(210deg, 40%, 98%);
  background-color: hsl(217deg, 33%, 17%);
  border-color: hsl(215deg, 20%, 65%);
}
input:focus::placeholder,
textarea:focus::placeholder {
  visibility: hidden;
}

.wrapper {
  position: relative;
}

.main__content {
  display: flex;
  flex-direction: column;
  margin-inline: 10%;
  gap: 5rem 0;
}

.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: hsl(222deg, 46%, 11%);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loader.fadeout {
  z-index: -1;
  opacity: 0;
  transition: all 0.2s ease;
}
.loader img {
  width: 4rem;
  height: auto;
  animation: loader 1.5s infinite ease;
}
@keyframes loader {
  to {
    transform: rotate(1turn);
  }
}

.like-form {
  position: absolute;
  top: 1rem;
  right: 1.5rem;
  z-index: 1;
  border: none;
  border-radius: 5rem;
  transform: scale(2);
}
.like-form .like-btn__btn {
  cursor: pointer;
}
.like-form .like-btn__btn .liked path {
  fill: hsl(348deg, 76%, 54%);
}
.like-form .like-btn__btn::before {
  content: "";
  position: absolute;
  top: 0.35rem;
  right: 0.3rem;
  width: 0.45rem;
  height: 0.7rem;
  border-top-left-radius: 0.55rem;
  border-top-right-radius: 0.5rem;
  border-bottom-left-radius: 0.1rem;
  box-shadow: 0px 0px 1rem 0.15rem #000;
  transform: rotate(-45deg);
  z-index: -1;
}
.like-form .like-btn__btn::after {
  content: "";
  position: absolute;
  top: 0.35rem;
  left: 0.3rem;
  width: 0.45rem;
  height: 0.7rem;
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.55rem;
  border-bottom-right-radius: 0.1rem;
  box-shadow: 0px 0px 1rem 0.15rem #000;
  transform: rotate(45deg);
  z-index: -1;
}
.like-form .like-btn__btn:hover {
  transform: scale(1.1);
}
.like-form .like-btn__btn:hover::before {
  top: 0.06rem;
}
.like-form .like-btn__btn:hover::after {
  top: 0.06rem;
}

h1,
h2,
h3 {
  font-weight: 700;
  line-height: 1.1;
  margin-top: 0;
}

p {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

